<template>
  <div class="chart">
    <div id="myCustomerNumber" style="width: 100%;height:260px;"></div>
  </div>
</template>

<script>
export default {
  name: 'Left',
  data () {
    return {
     
    }
  },
  methods: {
    drawmyCustomerNumber() {
      // 基于准备好的dom，初始化echarts实例【这里存在一个问题，请看到最后】
      let myCustomerNumber = this.$echarts.init(document.getElementById("myCustomerNumber"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        grid: {
			left: 10,
			right: 10,
			bottom: 0,
			top: 60,
			containLabel: true
		},
		// legend不显示的原因要与series一个子元素的name（设置name）必须保持一样的名称
		legend: {
			show:true,
			orient: 'horizontal',
			x : '85%',
			y : '0%',
			// align: 'right', // 图标和字体位置
            right: 'right',
			data: ['报修数量'],
			textStyle: {
				color: '#BFE9F7',
			},
			icon:'roundRect',
			itemWidth:8,
			itemHeight:8,
		},
        xAxis: {
          	data: ["5月", "6月", "7月", "8月", "9月", "10月", '11月', '12月', '1月', '2月', '3月'],
        	type: 'category',
			boundaryGap: true, // 从X轴最左边开始
          	axisLabel: {
				show: true,
				interval:0, //坐标刻度之间的显示间隔，默认就可以了（默认是不重叠）
               	rotate:45,  //调整数值改变倾斜的幅度（范围-90到90）
				textStyle: {
					color: 'rgba(255,255,255,0.65)', //x轴data 的颜色
					fontSize: 13,
					fontWeight: 'normal',
					verticalAlign: 'top',
					lineHeight: 30,
				}
			},
			axisTick: {
				show: false //不展示刻度
			},
          axisLine: {
              lineStyle: {
                  color: 'rgba(255,255,255,0.15)', // X轴刻度颜色
                  width: 1, //这里是为了突出显示加上的  
              }
          },
			nameTextStyle: {
				color: 'rgba(255,255,255,0.65)' //x轴名称的 颜色
			},
        },
        yAxis: {
          splitNumber : 5,
          axisLabel: {
			show: true,
			textStyle: {
				color: 'rgba(255,255,255,0.65)', //y轴data 的颜色
				fontSize: 12
			}
		},
		nameTextStyle: {
			color: 'rgba(255,255,255,0.15)' //y轴名称的 颜色
		},
          splitLine: {
			//网格样式
			show: true,
			lineStyle: {
				color: 'rgba(255,255,255,0.15)',
				width: 1,
				type: 'solid'
			}
		}
        },
        series: [
          {
			name:'报修数量',
            type: "bar",
			smooth: true,
			color:['#07C0FF'],  //折线条的颜色
            data: ['75', '30', '58', '82', '77', '30', '63', '58', '82', '76', '62'],
            symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			barWidth:'20%', //柱状宽度
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			}
		  }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myCustomerNumber.setOption(option);
    }
  },
  mounted() {
    this.drawmyCustomerNumber();
  } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
  .chart{
    width: 100%;
  }
</style>



