<template>
  <div class="chart">
    <img src="../../assets/img/operate/icon-leftOval-bg.png" class="chart-img" alt="">
    <div id="myChart" style="width:80%;height:150px"></div>
    <div class="contentBox">
      <p class="contentBox-title">能耗总量</p>
      <div class="contentBox-data">3715.94</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Left',
  data () {
    return {
      
    }
  },
  methods: {
    drawChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        series: [
          {
            name: "",
            type: "pie",
            color:['#F47153', '#0DFDF0', '#07C868', '#0086FF'],
            radius: ['70%', '95%'],
            hoveranimation:false,
            data: [
                {value: 3000, name: '电'},
                {value: 4532, name: '蒸汽'},
                {value: 4532, name: '水'},
                {value: 4532, name: '燃气'},
            ],
            label: {
                color: '#fff',
                fontSize: 16,
                alignTo: 'edge',
                // formatter: '{name|{b}}\n{value|{c}}/{d}%',
                formatter: '{name|{b}}\n{d}%',
                minMargin: 0,
                edgeDistance: 0,
                lineHeight: 25,
                rich: {
                    name: {
                        align: 'center',
                        fontSize: 16,
                        // color: '#999'
                    }
                }
            },
            labelLine: {
                length: 15,
                length2: 0,
                maxSurfaceAngle: 80
            },
            emphasis: {
              label: {
                  show: true
              },
              itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    }
  },
  mounted() {
    this.drawChart();
  } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
  .chart{
    width: 80%;
    margin: 42px auto 0;
    position: relative;
  }
  .chart-img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    /* width: 37%; */
    width: 170px;
  }
  #myChart{
    width: 100%;
    height: 100%;
    /* background: url('../../assets/img/operate/icon-leftOval-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat; */
    margin: auto;
  }
  .contentBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .contentBox .contentBox-title{
    font-family: PingFangSC-Regular;
    font-size: 14.13px;
    color: #BCF6D1;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
  .contentBox .contentBox-data{
    font-family: DINAlternate-Bold;
    font-size: 20px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
</style>



