<template>
   <div id="app">
     <div class="page">
       <!-- 头部 -->
       <div class="header">
         <div class="header-title">
           <img src="../../assets/img/operate/icon-header.png" alt="">
         </div>
         <div class="header-right">
           <span class="header-right__title">-2～10℃</span>
           <span class="header-right__title">2021.05.10 星期一 08:00</span>
         </div>
       </div>
       <div class="content">
         <div class="leftPage">
          <!-- 内容列表 -->
          <div class="dateList">
          <div class="dateList-wrapper">
              <div class="dateList-wrapper__item" v-for="(item,index) in dataList" :key="index">
                  <div class="dateList-wrapper__item-title">{{ item.title }}</div>
                  <div class="dateList-wrapper__item-data">
                      <span>{{ item.num }}</span><u>台</u>
                  </div>
              </div>
          </div>
          </div>
          <!-- 能耗费用 -->
          <div class="costList">
            <div class="tipsBox">
              <span>能耗费用</span>
            </div>
            <LeftOval></LeftOval>
          </div>
          <!-- 系统流量 -->
          <div class="costList">
            <div class="tipsBox">
              <span>系统流量</span>
            </div>
            <LeftFlow></LeftFlow>
          </div>
          <!-- 系统负荷 -->
          <div class="costList">
            <div class="tipsBox">
              <span>系统负荷</span>
            </div>
            <LeftLoad></LeftLoad>
          </div>
        </div>
        <div class="CenterPage">
          <!-- 中间内容列表 -->
          <div class="centerList">
          <div class="centerList-wrapper">
              <div class="centerList-wrapper__item">
                <img src="../../assets/img/operate/icon-01.png" alt="">
                  <div class="centerList-wrapper__item-title">服务用户</div>
                  <div class="centerList-wrapper__item-data">4523</div>
              </div>
              <div class="centerList-wrapper__item">
                <img src="../../assets/img/operate/icon-02.png" alt="">
                  <div class="centerList-wrapper__item-title">服务面积</div>
                  <div class="centerList-wrapper__item-data">约18.8万㎡</div>
              </div>
              <div class="centerList-wrapper__item">
                <img src="../../assets/img/operate/icon-03.png" alt="">
                  <div class="centerList-wrapper__item-title">系统能效</div>
                  <div class="centerList-wrapper__item-data">2640</div>
              </div>
          </div>
          </div>
          <!-- 中间饼图 -->
          <div class="centerOval">
            <div class="centerOval-wrapper">
              <div class="centerOval-wrapper__item">
                <div class="centerOval-wrapper__item-title" :class="{'centerOval-wrapper__item-title--active': tabsActive == 0 }" @click="changeTabsActive(0)">工单数</div>
                <div class="centerOval-wrapper__item-title" :class="{'centerOval-wrapper__item-title--active': tabsActive == 1 }" @click="changeTabsActive(1)">计划数</div>
                <div class="centerOval-wrapper__item-title" :class="{'centerOval-wrapper__item-title--active': tabsActive == 2 }" @click="changeTabsActive(2)">任务数</div>
                <div class="centerOval-wrapper__item-title" :class="{'centerOval-wrapper__item-title--active': tabsActive == 3 }" @click="changeTabsActive(3)">异常问题</div>
              </div>
            </div>
            <centerOval></centerOval>
          </div>
          <!-- 项目 -->
          <div class="projectList">
            <div class="tipsBox">
              <span>项目&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            </div>
            <centerProject></centerProject>
          </div>
        </div>
        <!-- 右边部分 -->
        <div class="rightPage">
          <div class="customerTop">
            <div class="customerTop-left">
              <div class="costList">
                <div class="tipsBox">
                  <span>客户报修</span>
                </div>
                <div class="customer">
                  <customerComplete></customerComplete>
                  <customerGuarantee></customerGuarantee>
                </div>
              </div>
              
            </div>
            <div class="customerTop-right">
              <div class="tipsBox">
                <span>客户满意度</span>
              </div>
              <customer></customer>
            </div>
          </div>
          <!-- 中间部分 客户报修数量比较 -->
          <div class="rightCenter">
            <div class="costList">
              <div class="tipsBox">
                <span>客户报修数量比较</span>
              </div>
              <customerNumber></customerNumber>
            </div>
          </div>
          <!-- 业态活跃度 -->
          <div class="rightActivity">
            <!-- <div class="costList"> -->
              <div class="tipsBox">
                <span>业态活跃度</span>
              </div>
              <rightActivity></rightActivity>
            <!-- </div> -->
          </div>
        </div>
       </div>
     </div>
   </div>
</template>
<script>
import rightActivity from '@/components/operate/rightActivity.vue';
import customerNumber from '@/components/operate/customerNumber.vue';
import customer from '@/components/operate/customer.vue';
import customerGuarantee from '@/components/operate/customerGuarantee.vue';
import customerComplete from '@/components/operate/customerComplete.vue';
import centerProject from '@/components/operate/centerProject.vue';
import centerOval from '@/components/operate/centerOval.vue';
import LeftLoad from '@/components/operate/LeftLoad.vue';
import LeftFlow from '@/components/operate/LeftFlow.vue';
import LeftOval from '@/components/operate/LeftOval.vue';
export default {
  components: { LeftOval, LeftFlow, LeftLoad, centerOval, centerProject, customerComplete, customerGuarantee, customer, customerNumber, rightActivity },
  name: 'operate',
  data () {
    return {
      dataList:[
          {title:'主机运行',num:'1'},{title:'冷/热泵运行',num:'1'},{title:'冷却泵运行',num:'1'},{title:'冷却塔',num:'1'},
      ],
      tabsActive: 0,
    }
  },
  methods:{
    changeTabsActive(index) {
      this.tabsActive = index;
    },
  }
}
</script>
<style scoped>
  .page{
    /* padding: 7px 27px 18px; */
    padding: 7px 27px 0px;
  }
  .header{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    position: relative;
  }
  .header .header-title{
    width: 65%;
  }
  .header .header-title img{
    width: 100%;
  }
  .header .header-right{
    position: absolute;
    right: 0;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
  }
  .header .header-right .header-right__title{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #CEF8FF;
    letter-spacing: 0;
    margin-top: 16px;
    margin-left: 10px;
    white-space: nowrap;
  }
  .content{
    display: flex;
    flex-direction: row;
    /* justify-content: space-between; */
    /* align-items: center; */
    justify-content: flex-start;
    align-items: flex-start;
  }
  .leftPage{
    background: url('../../assets/img/operate/icon-left-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 33px 27px 10px;
    width: 31.3%;
  }
  .dateList{
    background: url('../../assets/img/operate/icon-leftTop-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 20px 0;
    margin:  0 0 22px 0;
  }
  .dateList .dateList-wrapper{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
  }
  .dateList .dateList-wrapper__item{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 25%;
      border-right: 1px solid rgba(54,161,255,0.1);
  }
  .dateList .dateList-wrapper__item .dateList-wrapper__item-title{
      font-family: PingFangSC-Regular;
      font-size: 16.25px;
      color: rgba(255,255,255,0.65);
  }
  .dateList .dateList-wrapper__item 
  .dateList-wrapper__item-data{
      margin-top: 12px;
  }
  .dateList .dateList-wrapper__item 
  .dateList-wrapper__item-data span{
      font-family: DINAlternate-Bold;
      font-size: 37.92px;
      color: #3DDDFF;
  }
  .dateList .dateList-wrapper__item .dateList-wrapper__item-data u{
      font-family: PingFangSC-Regular;
      font-size: 16.25px;
      color: rgba(255,255,255,0.65);
      margin-left: 10px;
      text-decoration: none;
  }
  .costList{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 15px;
  }

  .CenterPage{
    width: 31.3%;
    margin: 0 16px;
  }
  .centerList{
    background: url('../../assets/img/operate/icon-leftTop-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 20px 0px;
  }
  .centerList .centerList-wrapper{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
  }
  .centerList .centerList-wrapper__item{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 25%;
      border-right: 1px solid rgba(54,161,255,0.1);
  }
  .centerList .centerList-wrapper__item img{ width: 62px;}
  .centerList .centerList-wrapper__item .centerList-wrapper__item-title{
      font-family: SourceHanSansCN-Normal;
      font-size: 18px;
      color: #B4F2FF;
      margin: 10px 0;
  }
  .centerList .centerList-wrapper__item .centerList-wrapper__item-data{
      font-family: PingFangSC-Semibold;
      font-size: 24px;
      color: #F6A93B;
      letter-spacing: 0;
  }

  .centerOval{
    background: url('../../assets/img/operate/icon-center-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 12.6px 17.2px 0 9.8px;
    margin: 16px 0 18px;
  }
  .centerOval .centerOval-wrapper{
    opacity: 0.7;
    background-image: linear-gradient(270deg, rgba(31,76,162,0.00) 0%, #1F4CA2 100%);
  }
  .centerOval .centerOval-wrapper__item{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .centerOval .centerOval-wrapper__item .centerOval-wrapper__item-title{
    font-family: SourceHanSansSC-Normal;
    font-size: 18px;
    color: #FFFFFF;
    width: 86.2px;
    text-align: center;
    padding: 11.4px 0 18.6px;
    position: relative;
    margin: 0 10px;
  }
  .centerOval .centerOval-wrapper__item .centerOval-wrapper__item-title.centerOval-wrapper__item-title--active::before{
    content: '';
    width: 86.2px;
    height: 3px;
    background: #B8E986;
    border-radius: 37.82px;
    border-radius: 37.82px;
    position: absolute;
    bottom: 0;
    right: 0;left: 0;
  }
  .projectList{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    /* margin-bottom: 15px; */
    padding: 24.5px 15px 24.5px 29px;
    background: url('../../assets/img/operate/icon-centerBottom-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }

  .rightPage{
    width: 33%;
  }
  .customerTop{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
  .customerTop .customerTop-left{
    background: url('../../assets/img/operate/icon-customerLeft-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 24px 29px;
    width: 373px;
  }
  .customer{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 36.1px 0 15px;
    width: 100%;
  }
  .customerTop .customerTop-right{
    background: url('../../assets/img/operate/icon-customerRight-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 24px 30px;
    width: 254px;
    margin-left: 7px;
  }

  .rightCenter{
    background: url('../../assets/img/operate/icon-rightCenter-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 24.5px 29.1px;
    margin: 18px 0 17px;
  }
  .rightActivity{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background: url('../../assets/img/operate/icon-rightCenter-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 24.5px 29.1px;
    /* margin: 18px 0 17px; */
  }
</style>


