<template>
  <div
    style="
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    "
  >
    <!-- 按钮 -->
    <div class="titleWhere">
      <div class="title">水</div>
      <div class="title2">电</div>
      <div class="title2">燃气</div>
      <div class="title2">蒸汽</div>
      
    </div>
    <!-- 折线图-->
    <div id="myChart" :style="{ width: '100%', height: '265px' }"></div>
  </div>
</template>

<script>
export default {
  name: "brokenLine",
  data() {
    return {};
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(
        document.getElementById("myChart"),
        "walden"
      );
      let that = this;
      // 绘制图表
      myChart.setOption({
        grid: {
          x: "60px",
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            "1月",
            "2月",
            "3月",
            "4月",
            "5月",
            "6月",
            "7月",
            "8月",
            "9月",
            "10月",
            "11月",
            "12月",
          ],
          /*改变x轴颜色*/
          axisLabel: {
            show: true,
            interval: 0,
            rotate: 40,
            textStyle: {
              color: "#A1D9FF",
            },
          },
          axisLine: {
            lineStyle: {
              color: "#23539B",
              width: 1, //这里是为了突出显示加上的
            },
          },
          //刻度颜色
          axisTick: {
            lineStyle: {
              color: "#FFFFFF",
              width: 1,
            },
            show: true,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            //网格线
            show: false,
          },
          /*字颜色*/
          axisLabel: {
            show: true,
            textStyle: {
              color: "#A1D9FF",
            },
          },
          //轴
          axisLine: {
            lineStyle: {
              color: "#23539B",
              width: 1, //这里是为了突出显示加上的
            },
            show: true,
          },
          //刻度颜色
          axisTick: {
            lineStyle: {
              color: "#FFFFFF",
              width: 1,
            },
            show: true,
          },
        },

        series: [
          {
            data: [
              8000,
              6000,
              4000,
              2000,
              1000,
              8000,
              6000,
              4000,
              2000,
              1000,
              8000,
              6000,
            ],
            type: "line",
            symbol: "circle", //实心圆
            areaStyle: {
              color: that.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(0,204,255,0.70)",
                },
                {
                  offset: 1,
                  color: "rgba(0,204,255,0.10)",
                },
              ]),
            },
            itemStyle: {
              normal: {
                color: "#00CCFF", //拐点颜色
                borderColor: "#00CCFF", //拐点边框颜色
                borderWidth: 3, //拐点边框大小
              },
              emphasis: {
                color: "#00CCFF", //hover拐点颜色定义
              },
            },
            lineStyle: {
              color: "#00CCFF", //改变折线颜色
            },
          },
        ],
      });
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>

<style>
.titleWhere {
  /* width:500px ; */
  display: flex;
  justify-content: space-between;
  margin-left: 55%;
}
.title {
  /* font-family: PingFangSC-Regular; */
  font-size: 12px;
  color: #e2ffff;
  width: 38px;
  height: 20px;
  background-image: linear-gradient(270deg, #0e55a8 1%, #3cb1fc 100%);
  border-right: 1px solid #72ccff;
  border-left: 1px solid #72ccff;
   margin-right: 5px;
}
.title2 {
  font-size: 12px;
  color: #e2ffff;
  width: 38px;
  height: 20px;
  background-image: linear-gradient(
    0deg,
    rgba(64, 173, 244, 0.09) 0%,
    rgba(30, 145, 229, 0.23) 100%
  );
  margin-right: 5px;
}
</style>


