<template>
    <div class="centerBox">
        <div class="chart">
            <div id="myComplete" style="width: 100%;height:99.1px;"></div>
            <div class="contentBox">
                <p class="contentBox-title">23%</p>
                <div class="contentBox-data">占比</div>
            </div>
        </div>
        <div class="title">完成率</div>
    </div>
</template>


<script>
export default {
  data () {
    return {
      titleList:[
          {title:'工单完成率',num:35,count:100},{title:'工单完成率',num:75,count:100},{title:'办理中数量',num:5155,count:10000},{title:'已完成',num:2353,count:10000},
      ],
      tabsActive: 0,
    }
  },
  methods:{
    drawmyComplete() {
      // 基于准备好的dom，初始化echarts实例
      let myComplete = this.$echarts.init(document.getElementById("myComplete"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ['75%', '90%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: 60,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: '#00FF94'},
                              {offset: 0.5, color: '#00FF94'},
                              {offset: 1, color: '#00FF94'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myComplete.setOption(option);
    }
  },
  mounted() {
    this.drawmyComplete();
  } 
}
</script>

<style scoped>  
  .centerBox{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .centerBox .chart{
      width: 99.1px;
      height: 99.1px;
      border-radius: 50%;
      position: relative;
  }
  .contentBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: url('../../assets/img/operate/icon-complete-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 70px;
    height: 70px;
  }
  .contentBox .contentBox-title{
    font-family: PingFangSC-Semibold;
    font-size: 20.69px;
    color: #07C0FF;
    letter-spacing: 0;
    text-align: center;
    line-height: 15.89px;
    margin-top: 10px;
  }
  .contentBox .contentBox-data{
    opacity: 0.7;
    font-family: PingFangSC-Regular;
    font-size: 10.34px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    line-height: 9.93px;
    margin-top: 10px;
  }
  .title{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #07C0FF;
    letter-spacing: 0;
    text-align: center;
  }
</style>


