<template>
	<div class="chart">
		<div id="myLeftLoad" style="width: 100%;height:220px;"></div>
	</div>
</template>

<script>
export default {
	name: 'Left',
	data () {
		return {

		}
	},
	methods: {
		drawMyLeftLoad() {
	// 基于准备好的dom，初始化echarts实例【这里存在一个问题，请看到最后】
	let myLeftLoad = this.$echarts.init(document.getElementById("myLeftLoad"));
	// 指定图表的配置项和数据
	let option = {
	tooltip: {
		transitionDuration: 0,
		trigger: 'axis',
		label: {
			color: '#fff',
			backgroundColor : '#333'
		},
		textStyle: { // 提示框浮层的文本样式。
			color: '#fff',
			fontStyle: 'normal',
			fontWeight: 'normal',
			fontFamily: 'sans-serif',
			fontSize: 12,
		},
		axisPointer: { // 坐标轴指示器配置项。
			type: 'line', // 'line' 直线指示器  'shadow' 阴影指示器  'none' 无指示器  'cross' 十字准星指示器。
			axis: 'auto', // 指示器的坐标轴。 
			snap: true, // 坐标轴指示器是否自动吸附到点上
			crossStyle: {
				color: '#999',
			},
			label: {
				color: '#fff',
				backgroundColor : 'rgba(255,61,110,0/97)'
			}
		},
		showContent: true, //是否显示提示框浮层，默认显示。
		// triggerOn: 'mouseover', // 触发时机  'mouseover'鼠标移动时触发。     'click'鼠标点击时触发。  'mousemove|click'同时鼠标移动和点击时触发。
		// enterable: false, // 鼠标是否可进入提示框浮层中，默认为false，如需详情内交互，如添加链接，按钮，可设置为 true。
		renderMode: 'html', // 浮层的渲染模式，默认以 'html 即额外的 DOM 节点展示 tooltip；
		// backgroundColor: '#000000',
		backgroundColor: "transparent",
		borderWidth: 1,
		padding: 0,
		formatter: function(params) {
			console.log("params:",params[0].data);
			// var str = `<div style = "background:url( ${require("../../assets/img/operate/icon-centerBottom-bg.png")}) no-repeat center center ;width:110.4px;height:52.8px;border-radius:5px;padding-top:10px;">
			var str = `<div style = "background: #000;width:110.4px;height:52.8px;border-radius:5px;padding-top:10px;">
			${params[0].name}:00<br/><p style="margin-top:5px;">${params[0].data}%</p></div>`;
			console.log(str);
			return str;
		},
		borderColor: '#1BCF9A',
		borderWidth: 0,
		extraCssText: 'box-shadow: 0 0 11px 0 #1BCF9A;background-image: linear-gradient(180deg, rgba(67,67,67,0.70) 0%, rgba(0,0,0,0.80) 100%);',
	},
	grid: {
	left: 10,
	right: 10,
	bottom: 0,
	top: 30,
	containLabel: true
	},
	xAxis: {
	data: ["00:05", "00:10", "00:15", "00:20", "00:25", "00:30"],
	type: 'category',
	// boundaryGap: false, // 从X轴最左边开始
	axisLabel: {
		show: true,
		textStyle: {
			color: 'rgba(255,255,255,0.65)', //x轴data 的颜色
			fontSize: 13
		}
	},
	axisLine: {
	lineStyle: {
	color: 'rgba(255,255,255,0.15)', // X轴刻度颜色
	width: 1, //这里是为了突出显示加上的  
	}
	},
	nameTextStyle: {
		color: 'rgba(255,255,255,0.65)' //x轴名称的 颜色
	},
	},
	yAxis: {
	splitNumber : 5,
	axisLabel: {
		show: true,
		textStyle: {
			color: 'rgba(255,255,255,0.65)', //y轴data 的颜色
			fontSize: 13
		}
	},
	nameTextStyle: {
		color: 'rgba(255,255,255,0.65)' //y轴名称的 颜色
	},
	splitLine: {
		//网格样式
		show: true,
		lineStyle: {
			color: 'rgba(255,255,255,0.15)',
			width: 1,
			type: 'solid'
		}
	}
	},
	series: [
		{
		type: "line",
		data: [20, 90, 60, 80, 20, 65, 38, 77, 82, 76],
		symbol: null, // 拐点图形类型
		symbolSize: 0, // 拐点图形大小
		markPoint: {
			symbol: 'path://../../assets/img/operate/icon-01.png', // 'circle', 'rect', 'roundRect', 'triangle', 'diamond', 'pin', 'arrow', path://m 0,0 h 48 v 20 h -30 l -6,10 l -6,-10 h -6 z,  path://m 0,0 h 48 v 20 h -34 l -6,10 l -6,-10 h -2 z
			symbolSize: function(val){
				return [textSize(toHSpeed(val, 2),"12px").width+5,40]
			},
			symbolOffset: ['34%', '-50%'],
			symbolKeepAspect: true,// 如果 symbol 是 path:// 的形式，是否在缩放时保持该图形的长宽比。
		},	
		itemStyle: {
			normal: {
				barGap: '-100%',
				color: '#0DFFB7 ',
				lineStyle: {
					width: 2, //调整 线条的宽度  5已经很宽啦
					color: '#0DFFB7' //线条颜色
				},
				label: {
					show: false, // 显示数字
					position: 'top',
					textStyle: {
						color: '#0DFFB7'
					}
				}
			}
		},
		smooth: true,
		areaStyle: {
			normal: {
				color: {
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0.9,
							color: '#123B71' // 下面处的颜色
						},
					{
							offset: 0.7,
							color: '#144273'
						},
						{
							offset: 0.6,
							color: '#1A5279'
						},
						{
							offset: 0,
							color: '#277A83' // 100% 上面处的颜色
						},
					],
					globalCoord: false // 缺省为 false
				}
			}
		}
	}
	]
	};
	// 使用刚指定的配置项和数据显示图表。
	myLeftLoad.setOption(option);
	}
	},
	mounted() {
	this.drawMyLeftLoad();
	} 
	}
	</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
	<style scoped>  
	.chart{
	width: 100%;
	}
	.company{
	font-family: PingFangSC-Regular;
	font-size: 16.25px;
	color: rgba(255,255,255,0.65);
	text-align: right;
	}
	</style>



