<template>
<div class="customerMain">
    <div class="centerBox">
        <div class="chart">
            <div id="myCustomer" style="width: 100%;height:103.8px;"></div>
            <div class="contentBox">
                <p class="contentBox-title">总数</p>
                <div class="contentBox-data">4,309</div>
            </div>
        </div>
    </div>
    <div class="customerBox">
      <div class="customerBox-wrapper">
        <div class="customerBox-list">
          <div class="customerBox-list__title">非常满意</div>
          <div class="customerBox-list__num">4,003<i>人</i></div>
        </div>
        <div class="customerBox-list">
          <div class="customerBox-list__title">满意</div>
          <div class="customerBox-list__num">4,003<i>人</i></div>
        </div>
        <div class="customerBox-list">
          <div class="customerBox-list__title">不满意</div>
          <div class="customerBox-list__num">12,290<i>人</i></div>
        </div>
      </div>
    </div>
</div>   
</template>


<script>
export default {
  data () {
    return {
      titleList:[
          {title:'工单完成率',num:35,count:100},{title:'工单完成率',num:75,count:100},{title:'办理中数量',num:5155,count:10000},{title:'已完成',num:2353,count:10000},
      ],
      tabsActive: 0,
    }
  },
  methods:{
    drawmyCustomer() {
      // 基于准备好的dom，初始化echarts实例
      let myCustomer = this.$echarts.init(document.getElementById("myCustomer"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        // legend不显示的原因要与series一个子元素的name（设置name）必须保持一样的名称
        // legend: {
        //   show:true,
        //   orient: 'horizontal',
        //   x : '10%',
        //   y : '50%',
        //   // align: 'center', // 图标和字体位置
        //   right: 'center',
        //   data: ['非常满意', '满意', '不满意'],
        //   textStyle: {
        //     color: '#DAFFE8'
        //   },
        //   icon:'roundRect',
        //   itemWidth:4,
        //   itemHeight:4,
        // },
        series: [
          {
            name: "非常满意",
            type: "pie",
            radius: ['65%', '75%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: '#02B1FF'},
                              {offset: 1, color: '#02B1FF'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 60,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          },
          {
            name: "满意",
            type: "pie",
            radius: ['50%', '60%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              { //中间最小
                value: 30,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: '#0DFBEF'},
                              {offset: 1, color: '#0DFBEF'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          },
          {
            name: "不满意",
            type: "pie",
            radius: ['80%', '90%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: 30,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                                {offset: 0, color: '#07C868'},
                                {offset: 1, color: '#07C868'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myCustomer.setOption(option);
    }
  },
  mounted() {
    this.drawmyCustomer();
  } 
}
</script>

<style scoped>  
  .customerMain{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 27px;
  }
  .centerBox{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: url('../../assets/img/operate/icon-guarantee-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin: 0 20px 0 27.9px;
  }
  .centerBox .chart{
      width: 103.8px;
      height: 103.8px;
      border-radius: 50%;
      position: relative;
  }
  #myCustomer{
    z-index: 2;
  }
  .contentBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: rgba(14,172,171,0.30);
    box-shadow: 0 1px 0 0 #035A7B;
    /* background-image: linear-gradient(270deg, #0E6392 0%, rgba(9,92,88,0.5) 100%); */
    z-index: 1;
    border-radius: 50%;
    width: 83.5px;
    height: 83.5px;
  }
  .contentBox .contentBox-title{
    font-family: PingFangSC-Regular;
    font-size: 14.13px;
    color: #BCF6D1;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
  .contentBox .contentBox-data{
    font-family: DINAlternate-Bold;
    font-size: 18.84px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
  }
  .customerBox{
    margin-top: 17.8px;
    width: 100%;
  }
  .customerBox .customerBox-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .customerBox .customerBox-list{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .customerBox-list .customerBox-list__title{
    font-family: MicrosoftYaHei;
    font-size: 10px;
    color: #DAFFE8;
    position: relative;
    white-space: nowrap;
    padding-left: 5.5px;
    margin-bottom: 4.5px;
  }
  .customerBox-list:nth-child(2){
    /* margin: 0 10.2px; */
  }
  .customerBox-list:nth-child(1) .customerBox-list__title::before{
    content: '';
    width: 4px;height: 4px;
    background: #07C868;
    position: absolute;left: 0;top: 50%;transform: translate(-50%);
  }
  .customerBox-list:nth-child(2) .customerBox-list__title::before{
    content: '';
    width: 4px;height: 4px;
    background: #0DFDF0;
    position: absolute;left: 0;top: 50%;transform: translate(-50%);
  }
  .customerBox-list:nth-child(3) .customerBox-list__title::before{
    content: '';
    width: 4px;height: 4px;
    background: #02B1FF;
    position: absolute;left: 0;top: 50%;transform: translate(-50%);
  }
  .customerBox .customerBox-list__num{
    font-family: DINAlternate-Bold;
    font-size: 12.4px;
    color: #FFFFFF;
    text-align: right;
    line-height: 13.95px;
    margin-top: 4.3px;
  }
  .customerBox .customerBox-list__num i{
    opacity: 0.7;
    font-family: MicrosoftYaHei;
    font-size: 10px;
    color: #0DFDF0;
    font-style: normal;
    margin-left: 8px;
  }
</style>


