import Vue from 'vue'
import VueRouter from 'vue-router'
import Operating from '../views/operating.vue'
import Operate from '../views/operate/operate.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Operating',
    component: Operating
  },
  {
    path: '/Operate',
    name: 'Operate', 
    component: Operate
  },
]

const router = new VueRouter({
  mode: 'hash', 
  base: process.env.BASE_URL,
  routes
})


export default router



