<template>
    <div class="centerBox">
        <div class="pressBox">
            <div class="press" v-for="(item,index) in titleList" :key="index">
                <div class="press-wrapper">
                    <div class="press-title">{{ item.title }}</div>
                </div>
                <div class="press-content">
                    <div class="press-content__chart">
                        <div class="press-content__chart-line" :style="{ width: ((item.num / item.count) *100) + '%' }"></div>
                    </div>
                </div>
                <div class="press-wrapper">
                    <div class="press-mix">{{ item.num }}%</div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
export default {
  data () {
    return {
      titleList:[
          {title:'办公',num:90,count:100},{title:'医院',num:15,count:100},{title:'商业',num:35,count:100},{title:'展示中心',num:38,count:100},{title:'餐饮',num:76,count:100},
      ],
      tabsActive: 0,
    }
  },
  methods:{
    drawmyCenterOval() {
      // 基于准备好的dom，初始化echarts实例
      let myCenterOval = this.$echarts.init(document.getElementById("myCenterOval"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ['70%', '95%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : true,
                        position : 'center',
                        textStyle : {
                            color : '#3D414B',
                            fontSize : '30',
                            fontFamily : '微软雅黑',
                            fontWeight : 'bold'
                        }
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                                {offset: 0, color: '#026AD1'},
                                {offset: 0.5, color: '#025EB9'},
                                {offset: 1, color: '#0255A7'}
                            ]
                        ),
                        borderWidth: 2,//设置边框粗细
                        borderColor: '#007EF7'//边框颜色
                    }
                }
              },
              {
                value: 60,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                                {offset: 0, color: '#139EBE'},
                                {offset: 0.5, color: '#16B5D9'},
                                {offset: 1, color: '#1189A5'}
                            ]
                        ),
                        borderWidth: 2,//设置边框粗细
                        borderColor: '#1CE7FF'//边框颜色
                    }
                }
              }
            ]
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myCenterOval.setOption(option);
    }
  },
  mounted() {
    this.drawmyCenterOval();
  } 
}
</script>

<style scoped>  
  .centerBox{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-top: 15px;
  }
  .press{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      position: relative;
      margin-top: 4.5px;
      background: rgba(0,129,255,0.15);
      padding: 7.3px 28px 8.2px 16.6px;
  }
  .press:nth-last-child(1),.press:nth-last-child(2){
    background: rgba(0,129,255,0.05);
  }
  .press .press-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .press .press-wrapper .press-title{
    font-family: PingFangSC-Medium;
    font-size: 16.58px;
    color: #A1D9FF;
    letter-spacing: 0;
    width: 70px;
    text-align: left;
  }
  .press .press-wrapper .press-mix{
    font-family: PingFangSC-Regular;
    font-size: 16.58px;
    color: #6DD2FF;
    letter-spacing: 0;
    line-height: 27.64px;
  }
  .pressBox{
      /* width: 315px;
      height: 100%; */
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
  }
  .press-content{
    position: relative;
    border: 1px solid rgba(0,129,255,0.30);
    border-radius: 138.19px;
    border-radius: 5.53px;
    margin: 0 18px 0 4.9px;
    height: 7.5px;
    padding: 0.5px 2px;
    /* width: 388.3px; */
    width: 360.3px;
  }
  .press-content .press-content__chart{
    width: 100%;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 5.5px;
    margin: 1px 0 2px;
  }
  .press-content .press-content__chart-line{
    height: 100%;
    border-radius: 138.19px;
    border-radius: 5.53px;
    background-image: linear-gradient(90deg, #07C0FF 0%, #00FF81 100%);
    margin: 2px;
  }
</style>


