<template>
<div class="guarantee">
    <div class="centerBox">
        <div class="chart">
            <div id="myGuarantee" style="width: 100%;height:99px;"></div>
            <div class="contentBox">
                <p class="contentBox-title">报修总数</p>
                <div class="contentBox-data">12,167</div>
            </div>
        </div>
    </div>
    <div class="guaranteeBox">
      <div class="guaranteeBox-wrapper">
        <div class="guaranteeBox-list">
          <div class="guaranteeBox-list__title">已处理数量</div>
          <div class="guaranteeBox-list__num">4,003</div>
        </div>
        <div class="guaranteeBox-list">
          <div class="guaranteeBox-list__title">未处理数量</div>
          <div class="guaranteeBox-list__num">12,290</div>
        </div>
      </div>
    </div>
</div>   
</template>


<script>
export default {
  data () {
    return {
      titleList:[
          {title:'工单完成率',num:35,count:100},{title:'工单完成率',num:75,count:100},{title:'办理中数量',num:5155,count:10000},{title:'已完成',num:2353,count:10000},
      ],
      tabsActive: 0,
    }
  },
  methods:{
    drawmyGuarantee() {
      // 基于准备好的dom，初始化echarts实例
      let myGuarantee = this.$echarts.init(document.getElementById("myGuarantee"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        // legend不显示的原因要与series一个子元素的name（设置name）必须保持一样的名称
        // legend: {
        //   show:true,
        //   orient: 'horizontal',
        //   x : '80%',
        //   y : '30%',
        //   // align: 'right', // 图标和字体位置
        //   right: 'right',
        //   data: ['已处理数量', '未处理数量'],
        //   textStyle: {
        //     color: '#DAFFE8'
        //   },
        //   icon:'roundRect',
        //   itemWidth:4,
        //   itemHeight:4,
        // },
        series: [
          {
            name: "未处理数量",
            type: "pie",
            radius: ['80%', '90%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: '#02B1FF'},
                              {offset: 1, color: '#02B1FF'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 60,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          },
          {
            name: "已处理数量",
            type: "pie",
            radius: ['60%', '70%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : false,
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              { //中间最小
                value: 30,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: '#0DFBEF'},
                              {offset: 1, color: '#0DFBEF'}
                            ]
                        ),
                    }
                }
              },
              {
                value: 100,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(32,102,255,0.20)'},
                              {offset: 1, color: 'rgba(0,172,255,0.20)'}
                            ]
                        ),
                    }
                }
              }
            ]
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myGuarantee.setOption(option);
    }
  },
  mounted() {
    this.drawmyGuarantee();
  } 
}
</script>

<style scoped>  
  .guarantee{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  }
  .centerBox{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: url('../../assets/img/operate/icon-guarantee-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin: 0 20px 0 27.9px;
  }
  .centerBox .chart{
      width: 99.1px;
      height: 99.1px;
      border-radius: 50%;
      position: relative;
  }
  #myGuarantee{
    z-index: 2;
  }
  .contentBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: linear-gradient(270deg, #0E6392 0%, rgba(9,92,88,0.5) 100%);
    z-index: 1;
    border-radius: 50%;
    width: 71px;
    height: 71px;
  }
  .contentBox .contentBox-title{
    font-family: PingFangSC-Regular;
    font-size: 11.9px;
    color: #BCF6D1;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
  .contentBox .contentBox-data{
    font-family: DINAlternate-Bold;
    font-size: 19.05px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
  }
  .guaranteeBox{
    display: flex;
    flex-direction: column;
  }
  .guaranteeBox .guaranteeBox-list{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .guaranteeBox-list:nth-child(2){
    margin-top: 8px;
  }
  .guaranteeBox-list .guaranteeBox-list__title{
    font-family: MicrosoftYaHei;
    font-size: 12px;
    color: #DAFFE8;
    position: relative;
    white-space: nowrap;
    padding-left: 5.5px;
    margin-bottom: 4.5px;
  }
  .guaranteeBox-list:nth-child(1) .guaranteeBox-list__title::before{
    content: '';
    width: 4px;height: 4px;
    background: #0DFDF0;
    position: absolute;left: 0;top: 50%;transform: translate(-50%);
  }
  .guaranteeBox-list:nth-child(2) .guaranteeBox-list__title::before{
    content: '';
    width: 4px;height: 4px;
    background: #02B1FF;
    position: absolute;left: 0;top: 50%;transform: translate(-50%);
  }
  .guaranteeBox .guaranteeBox-list__num{
    font-family: DINAlternate-Bold;
    font-size: 16px;
    color: #FFFFFF;
    text-align: right;
    line-height: 18px;
  }
</style>


