<template>
  <div class="chart">
    <div id="myCenterProject" style="width: 100%;height:250px;"></div>
  </div>
</template>

<script>
export default {
  name: 'Left',
  data () {
    return {
			series:[
				['20', '90', '60', '80', '20', '65', '38', '77', '82', '76'],
				['56', '45', '78', '55', '60', '38', '50', '19', '40', '60', '82', '76'],
				['0', '30', '18', '35', '2', '20', '15', '30', '20', '5', '10', '36'],
				['5', '60', '20', '49', '36', '78', '19', '18', '40', '85', '42', '36'],
				['80', '86', '80', '88', '76', '90', '70', '19', '92', '76', '80', '70'],
			]
    }
  },
  methods: {
    drawmyCenterProject() {
      // 基于准备好的dom，初始化echarts实例【这里存在一个问题，请看到最后】
      let myCenterProject = this.$echarts.init(document.getElementById("myCenterProject"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        grid: {
			left: 10,
			right: 0,
			bottom: 5,
			top: 60,
			containLabel: true
		},
		// legend不显示的原因要与series一个子元素的name（设置name）必须保持一样的名称
		legend: {
			show:true,
			orient: 'horizontal',
			x : '49.7%',
			y : '0%',
			// align: 'right', // 图标和字体位置
            right: 'right',
			data: ['类型1', '类型2', '类型3', '类型4', '类型5'],
			textStyle: {
				color: 'rgba(255,255,255,0.65)'
			},
			icon:'roundRect',
			itemWidth:8,
			itemHeight:8,
		},
        xAxis: {
          	data: ["00:05", "00:10", "00:15", "00:20", "00:25", "00:30"],
        	type: 'category',
			boundaryGap: false, // 从X轴最左边开始
          	axisLabel: {
				show: true,
				interval:0,
				textStyle: {
					color: 'rgba(255,255,255,0.65)', //x轴data 的颜色
					fontSize: 13
				}
			},
          axisLine: {
              lineStyle: {
                  color: 'rgba(255,255,255,0.15)', // X轴刻度颜色
                  width: 1, //这里是为了突出显示加上的  
              }
          },
					nameTextStyle: {
						color: 'rgba(255,255,255,0.65)' //x轴名称的 颜色
					},
        },
        yAxis: {
          splitNumber : 5,
          axisLabel: {
			show: true,
			textStyle: {
				color: 'rgba(255,255,255,0.65)', //y轴data 的颜色
				fontSize: 13
			}
		},
		nameTextStyle: {
			color: 'rgba(255,255,255,0.65)' //y轴名称的 颜色
		},
          splitLine: {
			//网格样式
			show: true,
			lineStyle: {
				color: 'rgba(255,255,255,0.15)',
				width: 1,
				type: 'solid'
			}
		}
        },
        series: [
          {
			name:'类型1',
      type: "line",
			smooth: true,
			color:['#62FBFF'],  //折线条的颜色
			// data: ['20', '90', '60', '80', '20', '65', '38', '77', '82', '76'],
			data: this.series[0],
			symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			}
		  },	
		  {
			name:'类型2',
      type: "line",
			smooth: true,
			color:['#36A1FF'],  //折线条的颜色
			// data: ['56', '45', '78', '55', '60', '38', '50', '19', '40', '60', '82', '76'],
			data: this.series[1],
			symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			},
          },
		  {
			name:'类型3',
      type: "line",
			smooth: true,
			color:['#E92B97'],  //折线条的颜色
			// data: ['0', '30', '18', '35', '2', '20', '15', '30', '20', '5', '10', '36'],
			data: this.series[2],
			symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			},
          },
		  {
			name:'类型4',
      type: "line",
			smooth: true,
			color:['#532BFF'],  //折线条的颜色
			// data: ['5', '60', '20', '49', '36', '78', '19', '18', '40', '85', '42', '36'],
			data: this.series[3],
			symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			},
          },
		  {
			name:'类型5',
      type: "line",
			smooth: true,
			color:['#06FE94'],  //折线条的颜色
			// data: ['80', '86', '80', '88', '76', '90', '70', '19', '92', '76', '80', '70'],
			data: this.series[4],
			symbol: null, // 拐点图形类型
			symbolSize: 0, // 拐点图形大小
			itemStyle: {
				label: {
					show: false, // 显示数字
				}
			},
          },
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myCenterProject.setOption(option);
    },
		generateRandomArr(n, min, max) {
        var arr = [];
        for (var i = 0; i < n; i++) {
            var random = Math.floor(Math.random() * (max - min + 1) + min);
            arr.push(random);
        }
        return arr;
    }
  },
  mounted() {
		this.drawmyCenterProject();
		this.timer = window.setInterval(() => {
      setTimeout(() => {
        ///调取接口获取数据
				this.series[0] = this.generateRandomArr(12, 20, 90);
				this.series[1] = this.generateRandomArr(12, 19, 78);
				this.series[2] = this.generateRandomArr(12, 0, 36);
				this.series[3] = this.generateRandomArr(12, 5, 85);
				this.series[4] = this.generateRandomArr(12, 19, 92);
        this.drawmyCenterProject();
      }, 0);
    }, 3000);
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>  
  .chart{
    width: 100%;
  }
</style>



