<template>
    <div class="centerBox">
        <div class="chart">
            <div id="myCenterOval" style="width: 100%;height:235px;"></div>
            <div class="contentBox">
                <p class="contentBox-title">工单数</p>
                <div class="contentBox-data">37,650</div>
            </div>
        </div>
        <div class="pressBox">
            <div class="press" v-for="(item,index) in titleList" :key="index">
                <div class="press-wrapper">
                    <div class="press-title">{{ item.title }}</div>
                    <div class="press-mix" v-if="index ==0 || index ==1">{{ item.num }}%</div>
                    <div class="press-mix" v-else>{{ item.num }}</div>
                </div>
                <div class="press-content">
                    <div class="press-content__chart">
                        <div class="press-content__chart-line" :style="{ width: (((item.count - item.num) / item.count) *100) + '%' }"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
export default {
  data () {
    return {
      titleList:[
          {title:'工单完成率',num:35,count:100},{title:'工单完成率',num:75,count:100},{title:'办理中数量',num:5155,count:10000},{title:'已完成',num:2353,count:10000},
      ],
      tabsActive: 0,
      // total:'',
      // item:'',
      // total:100,
      // item:60,
    }
  },
  methods:{
    drawmyCenterOval() {
      // 基于准备好的dom，初始化echarts实例
      let myCenterOval = this.$echarts.init(document.getElementById("myCenterOval"));
      // 指定图表的配置项和数据
      let option = {
        tooltip: {
            trigger: 'item',
            show: false // 不显示提示框
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ['70%', '95%'],
            hoveranimation:false,
            avoidLabelOverlap: false,
            itemStyle :{
                normal : {
                    label : {
                        show : true,
                        position : 'center',
                        textStyle : {
                            color : '#3D414B',
                            fontSize : '30',
                            fontFamily : '微软雅黑',
                            fontWeight : 'bold'
                        }
                    },
                    labelLine : {
                        show : false
                    }
                }
            },
            data: [
              {
                value: this.total,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                                {offset: 0, color: '#026AD1'},
                                {offset: 0.5, color: '#025EB9'},
                                {offset: 1, color: '#0255A7'}
                            ]
                        ),
                        borderWidth: 2,//设置边框粗细
                        borderColor: '#007EF7'//边框颜色
                    }
                }
              },
              {
                value: this.item,
                itemStyle: {
                    normal: {//颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上 
                        color: new this.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                                {offset: 0, color: '#139EBE'},
                                {offset: 0.5, color: '#16B5D9'},
                                {offset: 1, color: '#1189A5'}
                            ]
                        ),
                        borderWidth: 2,//设置边框粗细
                        borderColor: '#1CE7FF'//边框颜色
                    }
                }
              }
            ]
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myCenterOval.setOption(option);
    },
  },
  mounted() {
    this.drawmyCenterOval();
    let that=this;
		window.setInterval(() => {
		　　setTimeout(()=>{
        // this.total = 100;
        // this.item= 60;
        this.total = Math.floor(Math.random()*100);
        this.item = Math.floor(Math.random()*60);
        // console.log("項目:",this.total +'this.item'+ this.item);
				that.drawmyCenterOval();//我们的获取接口数据函数
				},0)
		}, 3000)
  } 
}
</script>

<style scoped>  
  .centerBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 52px 43.3px 40px;
  }
  .centerBox .chart{
      width: 235px;
      height: 235px;
      border-radius: 50%;
      position: relative;
  }
  .contentBox{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: url('../../assets/img/operate/icon-center-oval.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 145px;
    height: 145px;
  }
  .contentBox .contentBox-title{
    font-family: PingFangSC-Regular;
    font-size: 14.46px;
    color: #A1D9FF;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
  .contentBox .contentBox-data{
    font-family: PingFangSC-Medium;
    font-size: 24.4px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
    margin-top: 10px;
  }
  .press{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      position: relative;
      margin-top: 45px;
  }
  .press .press-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5.9px;
  }
  .press .press-wrapper .press-title{
    font-family: SourceHanSansCN-Normal;
    font-size: 16px;
    color: #B4F2FF;
  }
  .press .press-wrapper .press-mix{
    font-family: DINAlternate-Bold;
    font-size: 20px;
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 28.37px;
  }
  .pressBox{
      /* width: 315px;
      height: 100%; */
      margin-left: 61.6px;
  }
  .press-content{
    position: relative;
    background: rgba(255,255,255,.2);
    border-radius: 3.31px;
    height: 6px;
    width: 200px;
  }
  .press-content .press-content__chart{
    width: 100%;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .press-content .press-content__chart-line{
    height: 100%;
    border-radius: 3.31px;
  }
  .press:nth-child(1) .press-content .press-content__chart-line{background: #4A90E2;}
  .press:nth-child(2) .press-content .press-content__chart-line{background: #F42850;}
  .press:nth-child(3) .press-content .press-content__chart-line{background: #7ED321;}
  .press:nth-child(4) .press-content .press-content__chart-line{background: #F6A93B;}
</style>


