<template>
  <div class="lineBox">
    <!-- 电池格-->
    <div class="imageDiv">
      <div class="img">1</div>
      <div class="img">2</div>
      <div class="img">3</div>
      <div class="img">4</div>
      <div class="img">5</div>
      <div class="img">6</div>
    </div>
    <div id="BatteryChart" :style="{ width: '100%', height: '230px' }"></div>
    <div class="mvDiv">
      <div class="mv"><span class="num">512.5</span> mv</div>
      <div class="mv"><span class="num">448.7</span> mv</div>
      <div class="mv"><span class="num">300.2</span> mv</div>
      <div class="mv"><span class="num">200.2</span> mv</div>
      <div class="mv"><span class="num">100.2</span> mv</div>
      <div class="mv"><span class="num">10.2</span> mv</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BatteryLine",
  data() {
    return{
      
    }
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(
        document.getElementById("BatteryChart"),
        "walden"
      );
      let that = this;
      var category = [
        {
          name: "金融港二期",
          value: 512.5,
        },
        {
          name: "软件园五期",
          value: 448.7,
        },
        {
          name: "能源站1期",
          value: 300.2,
        },
        {
          name: "能源站2期",
          value: 120,
        },
        {
          name: "能源站3期",
          value: 10,
        },
        {
          name: "能源站3期",
          value: 110,
        },
      ]; // 类别
      var total = 1000; // 数据总数
      var datas = [];
      category.forEach((value) => {
        datas.push(value.value);
      });
      console.log("datas",datas);
      // 绘制图表
      myChart.setOption({
        // backgroundColor: '#071347',
        xAxis: {
          max: total,
          splitLine: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          axisLabel: {
            show: false,
          },
          axisTick: {
            show: false,
          },
        },
        grid: {
          left: 82,
          top: 9, // 设置条形图的边距
          right: 35,
          bottom: 20,
        },
        yAxis: [
          {
            type: "category",
            inverse: false,
            data: category,
            axisLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              show: false,
            },
          },
        ],
        series: [
          {
            // 内
            type: "bar",
            barWidth: 10,
            silent: true,
            itemStyle: {
              normal: {
                color: that.$echarts.graphic.LinearGradient(
                  0,
                  1,
                  0,
                  0,
                  [
                    {
                      offset: 0,
                      color: "#F7BC56", // 0% 处的颜色
                    },
                    {
                      offset: 0.8,
                      color: "#D3585B", // 60% 处的颜色
                    },
                  ],
                  false
                ),
              },
            },
            label: {
              normal: {
                formatter: "{b}",
                textStyle: {
                  color: "#DAFFE8",
                  fontSize: 12,
                  fontFamily: "ingFangSC-Regular",
                },

                position: "left",
                distance: 20, // 向右偏移位置
                show: true,
                rich: {
                  b: {
                    backgroundColor: {
                      image: "../../assets/1.png",
                    },
                  },
                },
              },
            },
            data: category,
            z: 1,
            animationEasing: "elasticOut",
          },
          // 分隔线
          {
            type: "pictorialBar",
            itemStyle: {
              normal: {
                color: "#00747B",
              },
            },
            symbolRepeat: "fixed",
            symbolMargin: 2,
            symbol: "rect",
            symbolClip: true,
            //分隔线宽度，高度
            symbolSize: [2, 9],
            symbolPosition: "start",
            //分隔位置
            symbolOffset: [3, -1],
            symbolBoundingData: this.total,
            data: [total, total, total, total, total, total],
            z: 2,
            animationEasing: "elasticOut",
          },
          {
            // label
            type: "pictorialBar",
            symbolBoundingData: total,
            itemStyle: {
              normal: {
                color: "none",
              },
            },
            label: {
              normal: {
                formatter: (params) => {
                  var text;
                  text =
                    "{f| " + ((params.data * 100) / total).toFixed(2) + "}";
                  return text;
                },
                rich: {
                  f: {
                    color: "#FFFFFF",
                  },
                },
                position: "right",
                distance: 10, // 向右偏移位置
                show: false,
              },
            },
            data: datas,
            z: 0,
          },
          //外框
          {
            name: "外框",
            type: "bar",
            barGap: "-130%", // 设置外框粗细
            data: [total, total, total, total, total, total],
            //宽度
            barWidth: 15,
            itemStyle: {
              normal: {
                barBorderRadius: [2, 2, 2, 2],
                color: "transparent", // 填充色
                barBorderColor: "#00747B", // 边框色
                barBorderWidth: 1, // 边框宽度
              },
            },
            z: 0,
          },
        ],
      });
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>

<style scoped>
.lineBox {
  display: flex;
  justify-content: space-around;
}
.img {
  margin-bottom: 15.5px;
  width: 16px;
  height: 18px;
  background-image: url("../../assets/排名数字背景-1.png");
  font-family: DINAlternate-Bold;
  font-size: 9px;
  color: #f9f660;
  letter-spacing: 0;
  text-align: center;
  text-shadow: 0 1px 1px rgba(249, 246, 96, 0.4);

  /* transform: translate(50%,50%); */
}
.imageDiv {
  padding-top: 15px;
  padding-left: 4%;
}
.mvDiv {
  padding-top: 15px;
  margin-right: 13%;
}
.mv {
  width: 76px;
  height: 18px;
  margin-bottom: 15.5px;
  /* opacity: 0.7; */
  font-family: MicrosoftYaHei;
  font-size: 10px;
  color: #0dfdf0;
  text-align: right;
  background-image: linear-gradient(to right, rgba(15,84,90,0.7), rgba(4,99,68,0.7));
   /* background-image:linear-gradient(to right,rgba(15,84,90,0.7),rgba(4,99,68,0.7)); */
   padding-right: 10px;
}
.num{
  font-family: DINAlternate-Bold;
font-size: 14px;
color: #FFFFFF;
letter-spacing: 0;
    text-align: left;
    margin-right: 10px;
}
</style>



